import "./styles.css";
import Button from "react-bootstrap/Button";
import { useState } from "react";
export default function App() {
  const [inputList, setInputList] = useState("sdfsdf");
  const itemEvent = (event) => {
    setInputList(event.target.value);
  };
  const [Items, setItems] = useState([]);

  const listOfItem = () => {
    setItems((oldItems) => {
      return [...oldItems, inputList];
    });
    setInputList("");
  };
  return (
    <div className="App">
      <h1>Create ToDo List</h1>
      <h2>Add multiple item add and delete</h2>
      <div className="card">
        <div className="card-header">TODO List</div>
        <div className="card-content">
          <div className="todo-list">
            <form className="formTodo">
              <input
                type="text"
                value={inputList}
                placeholder="Add item"
                onChange={itemEvent}
              />
              <Button className="add-todo" onClick={listOfItem}>
                +
              </Button>

              <ul>
                {Items.map((itemval) => {
                  return itemval && <li key={itemval}>{itemval}</li>;
                })}
              </ul>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
